@layer base {
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--fuente-texto);
  }
  html, body {
    height: 100%;
    min-height: 100%;
    margin: 0;
    padding: 0;
  }
  #app-root {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .layout-body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  .layout-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  .layout-main {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  footer {
    margin-top: auto;
  }
  h1, h2, h3, h4, h5, h6, p, a, input, textarea {
    margin: 0;
    padding: 0;
  }
  ul {
    list-style-type: none;
  }
  a {
    text-decoration: none;
  }
  p {
    font-family: var(--fuente-texto);
  }
  li a {
    font-family: var(--fuente-txt-decorado);
  }
  button {
    font-family: var(--fuente-btn);
    text-transform: lowercase;
    background-color: transparent;
    border: none;
  }
  #modal-root {
    position: fixed;
    inset: 0;
    z-index: 9990;
    pointer-events: none;
  }
  #modal-root>* {
    pointer-events: auto;
  }
}
@layer components {
  .icono-rosa {
    color: var(--color-principal);
    font-size: 1.8rem;
    cursor: pointer;
    display: inline-block;
  }
  .icono-azul {
