    font-size: 1.8rem;
    cursor: pointer;
    display: inline-block;
  }
  .eliminar {
    background: transparent;
    border: none;
    font-size: 1.2rem;
    color: var(--color-rojo-claro);
    cursor: pointer;
    transition: transform 0.2s ease;
    position: absolute;
    top: 8px;
    right: 8px;
  }
  .eliminar:hover {
    transform: scale(1.2);
    color: var(--color-rojo-fuerte);
  }
  .cerrar {
    font-weight: 700;
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: transparent;
    border: none;
    font-size: clamp(6rem, 5vw, 3rem);
    color: var(--color-texto-claro);
    cursor: pointer;
    z-index: 1002;
    transition: color 0.2s ease;
    line-height: 1;
  }
  .cerrar:hover, .cerrar:focus {
    color: var(--color-principal);
  }
  .btn-cta {
